.gallery {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 800px;
  margin: 5vmin 15vmin 5vmin 15vmin;
  overflow: hidden; }
  .gallery .card {
    flex: 1 1;
    height: 70vmin;
    position: relative;
    border: none;
    -webkit-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out; }
    .gallery .card p {
      position: absolute;
      top: 20px;
      right: 10px;
      left: 10px;
      max-width: 400px;
      padding: 10px;
      visibility: hidden;
      opacity: 0;
      background-color: rgba(14, 14, 14, 0.4);
      color: #fff5e8;
      font-size: calc(5px + 0.8vw); }
    .gallery .card h3 {
      position: absolute;
      bottom: 0;
      min-width: 100%;
      padding: 0.3em;
      background: rgba(14, 14, 14, 0.4);
      color: #fff5e8;
      font-size: calc(8px + 1.3vw);
      -webkit-transition: all 0.5s ease-in-out;
      transition: all 0.5s ease-in-out; }
    .gallery .card:hover {
      flex-grow: 1.5; }
      .gallery .card:hover img {
        -webkit-filter: opacity(100%);
                filter: opacity(100%); }
      .gallery .card:hover p {
        opacity: 1;
        visibility: visible;
        -webkit-transition-delay: 0.5s;
                transition-delay: 0.5s; }
    .gallery .card img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      -webkit-transition: all 1s ease-in-out;
      transition: all 1s ease-in-out;
      -webkit-filter: opacity(70%);
              filter: opacity(70%); }

